import React from "react";
import { useState } from "react";


export default function Project(props){
  const [procls,setprocls] = useState(false)
    return <>
    <div onMouseOver={()=>setprocls(true)} onMouseLeave={()=>setprocls(false)} className="pro-sub-div">
        <div id="pro-id" className="project-set">
          <img
            className="project-set-img"
            src={props.img}
            alt=""
          />
          <div id="pro-cover-1"  className="pro-img-cover"></div>
          <div id="pro-cover-2" className = { procls ? "pro-img-cover3": "pro-img-cover2"} ></div>
        </div>
        <div hidden={ procls ? false: true } className="pro-num">{props.num}</div>
        <div className = { procls ? "pro-name2": "pro-name"} >{props.name}</div>
        <div className = { procls ? "pro-desc2": "pro-desc"}>{props.desc}</div>
        <button onClick={()=>window.open(props.link, '_blank')} className="pro-btn">Preview</button>
      </div>
     </>
}