import React from "react";
import {useState} from "react";

export default function Contactform() {
  const [name, setname] = useState("");
  const [email, setemail] = useState("");
  const [message, setmessage] = useState("");


  function handleclick(event) {
    event.preventDefault();
    console.log("i got clicked");

    if (window.Email) {
      window.Email.send({
        Username: "saisudhakiransobila@gmail.com",
        Password: "F892602052A8DE3F9700BCE2A793FE9B06AB",
        Host: "smtp.elasticemail.com",
        Port: 2525,
        To: `${email}`,
        From: "saisudhakiransobila@gmail.com",
        Subject: "Thanks for Visiting",
        Body: " Hii there..!! "+`${name}`+
                ". I have received your message "+`${message}`+
                ". I will respond to your message as soon as possibile (this is auto generated response)..Thank You..",
      }).then((message) => {alert(message); setname(""); setemail(""); setmessage("") });
    }
  }

  return (
    <>
      <div className="label">Name *</div>
      <input
        style={{width: "100%"}}
        className="input"
        type="text"
        value={name}
        onChange={(e) => setname(e.target.value)}
      ></input>
      <div className="label">E-mail *</div>
      <input
        style={{width: "100%"}}
        className="input"
        onChange={(e) => setemail(e.target.value)}
        value={email}
        type="email"
        accept="email"
      ></input>
      <div className="label">Message *</div>
      <textarea
        style={{width: "100%"}}
        rows={4}
        className="input"
        value={message}
        onChange={(e) => setmessage(e.target.value)}
      ></textarea>
      <button
        onClick={handleclick}
        disabled={`${name}` && `${email}` && `${message}` ? false : true}
        class="cont-btn-abt"
        style={{
          width: 170,
          cursor:
            `${name}` && `${email}` && `${message}` ? "pointer" : "not-allowed",
          // background:`${name}` && `${email}` && `${message}` ? "" : "none",
          // backgroundColor:`${name}` && `${email}` && `${message}` ? "" :"#fa7e37"
        }}
      >
        <span
          class="btn-text"
          style={{
            color: `${name}` && `${email}` && `${message}` ? "" : "#e8e8e8",
          }}
        >
          Send Message
        </span>
      </button>
    </>
  );
}
